<template>
  <div class="md-layout" v-if="completeJobs">
    <div class="md-layout-item md-medium-size-100 md-xsmall-size-100 md-size-33" v-for="job in jobs" :key="job.id">
      <product-card header-animation="true">
        <img class="img" slot="imageHeader" :src="product1" />
        <md-icon slot="fixed-button">build</md-icon>
        <template slot="first-button">
          <md-icon>art_track</md-icon>
          <md-tooltip md-direction="bottom">View</md-tooltip>
        </template>
        <h4 slot="title" class="title">
          {{ job.clientName }}
        </h4>
        <div slot="description" class="card-description">
          {{ job.name }}
        </div>
        <template slot="footer">
          <div class="price">
            <i class="fas fa-money-bill-wave"></i> budget
            <h4 style="text-align:center;">{{ job.budget }}</h4>
          </div>
          <div class="price">
            <router-link :to="{ name: 'student-micro-status', params: {id: job.id} }"> 
              <md-button class="md-success">View</md-button>
            </router-link>
          </div>
          <div class="stats">
            <div class="price">
              <md-icon>place</md-icon> Location
              <h4 style="text-align:center;">{{ job.location }}</h4>
            </div>
          </div>
        </template>
      </product-card>
    </div>
  </div>
  <div v-else>
    <h1 class="black" style="text-align:center">You have no complete jobs</h1>
  </div>
</template>

<script>
import { ProductCard } from "@/components";
import db from '@/firebase/init';
import firebase from 'firebase/app';

export default {
  components: {
    ProductCard
  },
  data() {
    return {
      product1: "/img/dashboard/client/card-1.jpg",
      jobs:[],
      completeJobs: false
    };
  },
  methods: {
    deleteJob(id) {
      db.collection('jobs').doc(id).delete()
      .then(() => {
        this.jobs = this.jobs.filter(job => {
          return job.id != id;
        })
      })
    },
    editJob(id) {
      
    }
  },
  created() {
    window.scrollTo(0, 0);
    let user = firebase.auth().currentUser;
    let jobs = db.collection('jobs');
    jobs.where('studentId', '==', user.uid).where('status', '==', 'rate').get()
    .then(snapshot => {
      snapshot.forEach(doc => {
        this.completeJobs = true;
        let job = doc.data();
        job.id = doc.id;
        this.jobs.push(job);
      })
    })
  }
};
</script>
